import React from 'react';
import { Animated, Dimensions, Platform, StatusBar, StyleSheet, TouchableWithoutFeedback, View } from 'react-native';
import { Constants, ScreenOrientation, Video } from 'expo';
import Utils, { Logger } from './utils/Utils';

import API from './API';
import SkinUiza from './skins/uiza/Layout';
import PluginManager from './plugins/PluginManager';
import PlayerEvent from './Event';
import { ControlState, FullscreenState } from './skins/State';
import {NetInfo} from 'react-native';
import Message from './skins/uiza/Message';

export default class UZPlayer extends React.Component {
  constructor(props) {
    if (!!props.debug) {
      Logger.enableDebug = true;
    }
    super(props);


      // NetInfo.getConnectionInfo().then((connectionInfo) => {
      //     Logger.debug('Initial, type: ' + connectionInfo.type + ', effectiveType: '    + connectionInfo.effectiveType);
      // });
      // function handleFirstConnectivityChange(connectionInfo) {
      //     Logger.debug('First change, type: ' + connectionInfo.type + ', effectiveType: ' + connectionInfo.effectiveType);
      //     NetInfo.removeEventListener(
      //         'connectionChange',
      //         handleFirstConnectivityChange
      //     );
      // }
      // NetInfo.addEventListener(
      //     'connectionChange',
      //     handleFirstConnectivityChange
      // );
    this.initTime = Date.now();

    let self = this;

    const size = Dimensions.get('screen');

    this.state = {
      playbackStatus: {},
      fullsceenState: FullscreenState.EXIT,
      fullscreenWidth: size.width > size.height ? size.width : size.height,
      fullscreenHeight: size.height < size.width ? size.height : size.width,
    };

    this.player = {
      VERSION: '1.0.0'
    };
    this.container = {};

    Promise.resolve(Constants.getWebViewUserAgentAsync()).then(function(res) {
      self.player.userAgent = res;
    });
    this.player.deviceId = Constants.deviceId;
    this.player.sessionId = Constants.sessionId;
    this.player.didJustFinish = false;
    this.player.inited = false;
    this.player.props = props;

    this.player.listener = {
      onError: function(evt) {
        Logger.debug('onError');
        if (self.player.props && self.player.props.listener && typeof self.player.props.listener.onError === 'function') {
          self.player.props.listener.onError(evt);
        }
      },
      onFullscreenUpdate: function(evt) {
        Logger.debug('onFullscreenUpdate');
        // switch (evt.fullscreenUpdate) {
        //   case PlayerEvent.FULLSCREEN_WILL_ENTER:
        //     self.state.fullsceenState = FullscreenState.ENTERING;
        //     break;
        //   case PlayerEvent.FULLSCREEN_DID_ENTER:
        //     self.state.fullsceenState = FullscreenState.ENTER;
        //     break;
        //   case PlayerEvent.FULLSCREEN_WILL_EXIT:
        //     self.state.fullsceenState = FullscreenState.EXITING;
        //     break;
        //   case PlayerEvent.FULLSCREEN_DID_EXIT:
        //     self.state.fullsceenState = FullscreenState.EXIT;
        //     break;
        // }
        if (self.player.props && self.player.props.listener && typeof self.player.props.listener.onFullscreenChange === 'function') {
          self.player.props.listener.onFullscreenChange(evt);
        }

        self.skin.updateState({ fullscreen: evt === FullscreenState.ENTER });
      },
      onLoad: function(evt) {
        Logger.debug('onLoad');
        // self.player.video.setPositionAsync(60000);
        if (self.player.props && self.player.props.listener && typeof self.player.props.listener.onLoaded === 'function') {
          self.player.props.listener.onLoaded(evt);
        }
      },
      onLoadStart: function(evt) {
        Logger.debug('onLoadStart');
        // self.player.video.initialStatus({ positionMillis: 60000 });
        if (self.player.props && self.player.props.listener && typeof self.player.props.listener.onLoad === 'function') {
          self.player.props.listener.onLoad(evt);
        }
      },
      onPlaybackStatusUpdate: function(evt) {
        Logger.debug('onPlaybackStatusUpdate');
        self.state.playbackStatus = evt;
        if (self.state.playbackStatus.didJustFinish) {
          self.player.didJustFinish = true;
          self.skin.show();
        } else if (self.state.playbackStatus.isPlaying) {
          self.player.didJustFinish = false;
        }
        if (self.player.props && self.player.props.listener && typeof self.player.props.listener.onPlaybackChange === 'function') {
          self.player.props.listener.onPlaybackChange(evt);
        }
        self.skin.updateState({ playbackStatus: self.state.playbackStatus });
        self.pluginManager.updatePlaybackStatus(self.state.playbackStatus);
      },
      onReadyForDisplay: function(evt) {
        Logger.debug('onReadyForDisplay');
      }
    };

    this.pluginManager = new PluginManager(this.player.props);

    this.errorMessage = null;
  }

  static get Event() {
    return PlayerEvent;
  }

  async init() {
    this.entityDetail = null;
    this.liveDetail = null;
    if (this.player.props.stream === 'vod') {
      this.entityDetail = await API.getEntityDetail(this.player.props.api, this.player.props.token, this.player.props.entityId);
    }else if (this.player.props.stream === 'live') {
        this.entityDetail = await API.getLiveDetail(this.player.props.api, this.player.props.token, this.player.props.entityId,this.player.props.feedId, null);
      this.liveDetail = await API.getLiveView(this.player.props.api, this.player.props.token, this.player.props.entityId, null);
    }

    this.player.playbackToken = await API.getPlaybackToken(this.player.props.api, this.player.props.appId, this.player.props.entityId);

    this.player.cdnListFull = [];
    this.player.cdnList = [];

    let res = null;
    if (this.player.props.stream === 'vod') {
      res = await API.getCDNVod(this.player.props.env, this.player.playbackToken,
        this.player.props.appId, this.player.props.entityId);
    } else if (this.player.props.stream === 'live') {
      res = await API.getCDNLive(this.player.props.env, this.player.playbackToken,
        this.player.props.appId, this.player.props.entityId,
        this.player.props.streamName, this.player.props.region);
    }

    if (res && res.data && res.data.cdn) {
      this.player.cdnListFull = res.data.cdn
      this.player.cdnList = []
      await Promise
        .all(res.data.cdn)
        .then(cdns => {
          return cdns.map(cdn => {
            this.player.cdnList.push(cdn.host);
          });
        });
    }
    else {
      Logger.debug('init() res', res);
      this.skin.updateLiveStatusProps(res.message);
      return;
    }
    this.pluginManager.init(this.player, {
      cdnList: this.player.cdnList,
      entityDetail: this.entityDetail
    });
    this.skin.updateEntityProps({ entityDetail: this.entityDetail });

    this.player.inited = true;

    return this.player.inited;
  }

  get didJustFinish() {
    return this.player.didJustFinish;
  }

  toggleFullscreen() {
    Logger.debug('toggleFullscreen: ', this.state.fullsceenState);
    try {
      if (this.state.fullsceenState === FullscreenState.ENTER || this.state.fullsceenState === FullscreenState.ENTERING) {
        this.exitFullscreen();
        this.player.listener.onFullscreenUpdate(FullscreenState.EXIT);
      } else {
        this.enterFullscreen();
        this.player.listener.onFullscreenUpdate(FullscreenState.ENTER);
      }
    } catch(e) {}
  }

  enterFullscreen() {
    try {
      // this.player.video.presentFullscreenPlayer();
      this.state.fullsceenState = FullscreenState.ENTERING;
      StatusBar.setHidden(true);
      this.state.fullsceenState = FullscreenState.ENTER;

      this.setState(previousState => {
        return { fullsceenState: FullscreenState.ENTER };
      });
      ScreenOrientation.allow(ScreenOrientation.Orientation.LANDSCAPE);
    } catch(e) {}
  }

  exitFullscreen() {
    try {
      // this.player.video.dismissFullscreenPlayer();
      this.state.fullsceenState = FullscreenState.EXITING;
      StatusBar.setHidden(false);
      this.state.fullsceenState = FullscreenState.EXIT;

      this.setState(previousState => {
        return { fullsceenState: FullscreenState.EXIT };
      });
      ScreenOrientation.allow(ScreenOrientation.Orientation.ALL);
    } catch(e) {}
  }

  togglePlayPause() {
    try {
      if (this.state.playbackStatus.isPlaying) {
        this.pause();
      } else {
        this.play();
      }
    } catch(e) {}
  }

  pause() {
    try {
      this.player.video.pauseAsync();
      this.skin.show();
    } catch (e) { }
  }

  /* position in second */
  play(position) {
    try {
      if (this.player.didJustFinish) {
        this.player.didJustFinish = false;
        this.pluginManager.emit('replay');
      }
      if (typeof position === 'undefined') {
        this.player.video.playAsync();
      } else {
        this.player.video.playFromPositionAsync(position * 1000);
      }
      this.skin.show();
    } catch (e) { }
  }

  /* position in second */
  backward(position) {
    try {
      if (this.state.playbackStatus) {
        let positionMillis = position * 1000;
        positionMillis = this.state.playbackStatus.positionMillis - positionMillis;
        if (positionMillis < 0) {
          positionMillis = 0;
        }
        this.player.video.setPositionAsync(positionMillis);
      }
    } catch (e) { }
  }

  /* position in second */
  forward(position) {
    try {
      if (this.state.playbackStatus) {
        let positionMillis = position * 1000;
        positionMillis = this.state.playbackStatus.positionMillis + positionMillis;
        if (positionMillis > this.state.playbackStatus.durationMillis - 1e4) {
          positionMillis = this.state.playbackStatus.durationMillis - 1e4;
        }
        this.player.video.setPositionAsync(positionMillis);
      }
    } catch (e) { }
  }

  seek(position, forceSeek = false) {
    try {
      let positionMillis = position * 1000;
      if (!forceSeek && positionMillis > this.state.playbackStatus.durationMillis - 2000) {
        positionMillis = this.state.playbackStatus.durationMillis - 2000;
      }
      if (positionMillis < 0) {
        positionMillis = 0;
      }
      this.player.video.setPositionAsync(positionMillis);
    } catch (e) { }
  }

  // shouldComponentUpdate(nextProps, nextState) {
  //   return false;
  // }

  render() {
    const size = Dimensions.get('screen');
    const height169 = Math.floor(size.width * 9 / 16);

    const view = (
      <View {...this.props}
        style={[styles.container, this.state.fullsceenState == FullscreenState.ENTER ? { position: 'absolute', zIndex: 9999, top: 0, left: 0, height: this.state.fullscreenHeight, width: this.state.fullscreenWidth } : { height: height169 }]}
        ref={
          component => {
            this.container = component;
          }
        }>
        <Video
          ref={
            component => {
              this.player.video = component;
            }
          }
          useNativeControls={false}
          isLooping={false}
          resizeMode={Expo.Video.RESIZE_MODE_CONTAIN}
          style={styles.video}
          onError={this.player.listener.onError}
          onFullscreenUpdate={this.player.listener.onFullscreenUpdate}
          onLoad={this.player.listener.onLoad}
          onLoadStart={this.player.listener.onLoadStart}
          onPlaybackStatusUpdate={this.player.listener.onPlaybackStatusUpdate}
          onReadyForDisplay={this.player.listener.onReadyForDisplay}
        />
        <SkinUiza
          ref={
            component => {
              this.skin = component;
            }
          }
          style={styles.skin}
          stream={this.player.props.stream}
          player={this}
          playbackStatus={this.state.playbackStatus}
          entityDetail={this.entityDetail}
          playerProps={this.player.props}
          liveDetail={this.liveDetail}
        />
      </View>
    );

    let self = this;
    Promise.resolve(self.init()).then(async function(res) {
      if (self.player.video) {
        try {
          if (self.player.props && self.player.props.listener &&
            typeof self.player.props.listener.onReady === 'function') {
            self.player.props.listener.onReady();
          }
          let url = null;
          if (self.player.props.stream === 'vod') {
            url = self.definedLinkPlayVod();
          } else if (self.player.props.stream === 'live') {
            url = self.definedLinkPlayLive();
          }

          if (self.player.props.stream === 'vod') {
            if(url.indexOf('undefined')!= -1) {
              self.skin.updateLiveStatusProps('Content not found!!!');
              return;
            }
          } else if (self.player.props.stream === 'live') {
            let liveStatusDetail = await API.getLiveStatusDetail(self.player.props.api, self.player.props.token, self.player.props.entityId);
            if(liveStatusDetail && liveStatusDetail.lastProcess !== 'start'){
              self.skin.updateLiveStatusProps('This content is not streaming now!!!');
              return;
            }
          }

          await self.player.video.loadAsync({ uri: url });
          await self.player.video.setStatusAsync({
            positionMillis: 0, // for resume
            isLooping: false,
            shouldPlay: true
          });
        } catch (e) {
          self.skin.updateState({ playbackStatus: self.state.playbackStatus });
          self.skin.hide();
          Logger.debug('error 377: ', e.message);
        }
      }
    });

    this.readyTime = Date.now();
    this.player.pageLoadTime = this.readyTime - this.initTime;

    return view;
  }

  definedLinkPlayVod() {
    try {
      let prefix = 'playlist_ts.m3u8';
      if (Platform.OS === 'ios' && parseInt(Platform.Version, 10) >= 10) { // iOS >= 10
        prefix = 'playlist.m3u8'; // hls
      } else if (Platform.OS === 'android' && parseInt(Platform.Version, 10) >= 16) { // android API 16 ~ android 4.1
        prefix = 'manifest.mpd'; // dash
      }
      if (this.player.props.playerName === 'vod') {
        prefix = 'playlist_ts.m3u8';
      }

      return `https://${this.player.cdnList[0]}/${this.player.props.appId}-stream/${this.player.props.entityId}/package/${prefix}`
    } catch(e) {
      self.skin.updateState({ playbackStatus: self.state.playbackStatus });
      self.skin.hide();
      Logger.debug('error: ', e.message);
      return null;
    }
  }

  definedLinkPlayLive() {
    try {
      let prefix = 'playlist_ts.m3u8';
      if (Platform.OS === 'ios' && parseInt(Platform.Version, 10) >= 10) { // iOS >= 10
        prefix = 'playlist.m3u8'; // hls
      } else if (Platform.OS === 'android' && parseInt(Platform.Version, 10) >= 16) { // android API 16 ~ android 4.1
        prefix = 'manifest.mpd'; // dash
      }
      if (this.player.props.playerName === 'vod') {
        prefix = 'playlist_ts.m3u8';
      }

      return `https://${this.player.cdnList[0]}/${this.player.props.appId}-live/${this.player.props.streamName}/${prefix}`
    } catch(e) {
      self.skin.updateState({ playbackStatus: self.state.playbackStatus });
      self.skin.hide();
      Logger.debug('error: ', e.message);
      return null;
    }
  }

    onMutePressed = () => {
        if (this.player.video != null) {
            this.player.video.setIsMutedAsync(!this.state.playbackStatus.isMuted);
        }
    };
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: 'black',
    width: '100%',
  },
  video: {
    position: 'absolute',
    zIndex: 0,
    top: 0,
    left: 0,

    width: '100%',
    height: '100%',
  },
  skin: {
    position: 'absolute',
    zIndex: 99999,
    top: 0,
    left: 0,

    width: '100%',
    height: '100%',
  }
});
